/* nav_bar ----------*/
#nav_bar {
  display: none;
  width: 25px;
  height: 17px;
  padding: 0 12px;
  position: relative;
  vertical-align: middle;
}
#nav_bar:hover {
  cursor: pointer;
}
#nav_bar span {
  position: relative;
  display: block;
  width: inherit;
  height: inherit;
}
#nav_bar:after,
#nav_bar span:before,
#nav_bar span:after {
  content: "";
  position: absolute;
  display: block;
  width: 25px;
  height: 3px;
  border-radius: 0.2rem;
  background-color: #fff;
  left: 0;
  top: calc( 50% + 5.5px);
  -webkit-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
}
#nav_bar:after {
  top: 0;
  left: 12px;
}
#nav_bar span:before {
  top: calc( 50% - 1.5px);
}
#nav_bar span:after {
  bottom: 28%;
}
#nav_bar:hover:after {
  top: 0;
}
#nav_bar:hover span:after {
  bottom: 35%;
}
.overflow #nav_bar:after {
  top: calc(50% - 3px);
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
}
.overflow #nav_bar span:before {
  opacity: 0;
}
.overflow #nav_bar span:after {
  top: calc(50% - 3px);
  -webkit-transform: rotate(-135deg);
          transform: rotate(-135deg);
}
.changeLan {
  color: #fff;
  background: transparent;
  border: 0.05rem solid #FFFFFF;
  height: 1.5rem;
  line-height: 1.5rem;
  font-size: 0.8rem;
  padding: 0 0.75rem;
  position: relative;
  overflow: visible;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: inline-block;
  vertical-align: middle;
  border-radius: 2rem;
  border-color: #ffffff;
  z-index: 0;
  -webkit-transition: color 0.3s ease, background 0s ease;
  transition: color 0.3s ease, background 0s ease;
}
.changeLan:first-child {
    margin-right: 0.6rem;
}
.changeLan:before {
  position: absolute;
  content: '';
  background: #ffffff;
  -webkit-transition-duration: .3s;
          transition-duration: .3s;
  z-index: -1;
  top: 0;
  right: auto;
  bottom: auto;
  left: 0;
  width: 0;
  height: 100%;
  opacity: 1;
  border-radius: 2rem;
}
.changeLan:hover {
  color: #EF2E69;
  background: #ffffff;
  -webkit-transition: color 0.3s ease, background 0s 0.3s ease;
  transition: color 0.3s ease, background 0s 0.3s ease;
}
.changeLan:hover:before {
  width: 100%;
  height: 100%;
  opacity: 1;
}
