
.event-list {
  width: 50rem;
  margin: 0 auto;
  padding: 2rem 1rem;
  overflow: hidden;
}
.event-list li {
    float: left;
    width: 23rem;
    margin-bottom: 2.5rem;
}
.event-list li .event-imgContent {
      width: 100%;
      display: block;
      height: 17.65rem;
      overflow: hidden;
      padding: 0.1rem;
      border: 0.05rem solid #d7d7d7;
      border-radius: 0.3rem;
      margin-bottom: 1rem;
}
.event-list li img {
      display: block;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      width: 100%;
      border-radius: 0.3rem;
      -webkit-transition: -webkit-transform 0.5s ease;
      transition: -webkit-transform 0.5s ease;
      transition: transform 0.5s ease;
      transition: transform 0.5s ease, -webkit-transform 0.5s ease;
}
.event-list li:nth-child(2n-1) {
    margin-right: 4rem;
}
.event-imgContent:hover,
.event-imgContent:focus {
  opacity: 1;
}
.event-imgContent:hover img,
  .event-imgContent:focus img {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.event-imgTitle {
  font-size: 1.2rem;
  color: #FC4083;
  font-weight: 600;
}
.event-imgDesc {
  font-size: 1rem;
  color: #999999;
}
.article-p {
  text-align: left;
  margin-bottom: 1.5rem;
}
.imgDesc {
  font-weight: 600;
  margin: 0.75rem 0 1.5rem;
}
@media screen and (max-width: 900px) {
.event-list {
    width: 100vw;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 2rem;
}
.event-list li {
      width: 48%;
}
.event-list li:nth-child(2n-1) {
      margin-right: 4%;
}
}
@media screen and (max-width: 500px) {
.event-list {
    padding: 18px 12px;
}
.event-list li {
      float: none;
      width: 100%;
      margin-bottom: 25px;
}
.event-list li .event-imgContent {
        margin-bottom: 10px;
}
.event-list li:nth-child(2n-1) {
      margin-right: 0;
}
}



